<template>
  <div id="videoList">
    <div id="videoChannel" ref="videoChannel" style="z-index: 12" class="prevent" v-if="videoNav.length > 0">
      <div class="v-video-channelbox noScrollBar prevent">
        <div v-for="(item,index) in videoNav" class="prevent" :class="activeIndex === index ? 'active':''" @click="switchVideo(item,index)" :key="index">
          {{item.ch}}
        </div>
      </div>
    </div>
    <div class="wrapper" ref="wrapper" :class="{isBrowser: $config.source_style < 6}">
      <div class="bg-fff" ref="bg_fff">
        <transition name="topTxt">
          <div class="text-center lh66 topTxt" v-if="topBol">
            {{num!==0?"为您推荐"+num+"条更新":"暂无更新，请休息会再来~"}}
          </div>
        </transition>
        <video-list ref="muAdRooot" :loading="loading" @digg_status='digg_status' @deleteAd="deleteAd" :videoList="list" @videoDigg="videoDigg" @upDate="upDate"></video-list>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import videoList from '../common/video-list'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'videoCommon',
  props: {
    columnid: {
      type: Number,
      default: 0
    },
    swichId: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      loading: 0,
      topList: [],
      page: 1,                    // 置顶列表
      popupVisible: false,        // 弹出框状态
      topBol: false,              // 下滑是显示文字的bol控制
      num: 0,                     // 下拉刷新了几条
      indexGray: null,            // 要被置灰的下标
      timer: null,                // 单次定时器
      downBol: false,             // 是否开始下拉加载
      lasttime: 0,                // 上一次下拉时间
      videoNav: [],
      activeIndex: 0,
      list: []
    }
  },
  methods: {
    ...mapMutations([
      'setVideoPage',
      'comment_video_list'
    ]),
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {
      })
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.muAdRooot.$el.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            console.log(this.setBrowser)
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    deleteAd (index) {
      this.list.splice(index, 1)
    },
    initScroll () {
      this.$nextTick(() => {
        if (this.$refs.videoChannel) {
          if (!this.videoScroll) {
            this.videoScroll = new BScroll(this.$refs.videoChannel, {
              click: true,
              scrollY: true,
              scrollX: true,
              bounceTime: 100,
              mouseWheel: {
                speed: 10,
                invert: false,
                easeTime: 300
              }
            })
          } else {
            this.videoScroll.refresh()
          }
        }
      })
    },
    // 切换id
    switchVideo (item, index) {
      // this.$indicator.open({
      //   text: '加载中...',
      //   spinnerType: 'fading-circle'
      // })
      this.page = 1
      this.timer = setTimeout(() => {
        if (this.timer) {
          clearTimeout(this.timer)
          this.timer = null
        }
        this.activeIndex = index
        this.getVideoList()
      }, 100)
    },
    videoDigg (index) {
      this.$set(this.list[index], 'video_like_count', this.list[index].video_like_count ? ++this.list[index].video_like_count : 1)
      this.$set(this.list[index], 'is_fabulous', 1)
    },
    // 点击状态
    digg_status ({index, value}) {
      this.$set(this.list[index], 'digg_status', value)
    },
    // 加载上拉下拉组件
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            click: true,
            probeType: 3,
            pullDownRefresh: {
              threshold: 100,
              stop: 0
            },
            pullUpLoad: true,
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            bounceTime: 400,
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downList()
          })
          this.scroll.on('pullingUp', () => {
            this.upList()
          })
          this.scroll.on('scroll', (pos) => {
            this.scrollFn()
            if (pos.y < 80 && pos.y > -80) {
              this.$emit('topscroll', pos.y)
            }
          })
        } else {
          this.$nextTick(() => {
            this.scroll.refresh()
          })
        }
      })
    },
    // 上次更新点击事件
    refresh () {
      if (this.scroll) {
        this.scroll.scrollTo(0, 0, 200)
      }
      this.downList()
    },
    // 获取下拉列表
    downList () {
      // 如果没有目录直接弹出函数
      if (this.videoNav.length === 0) {
        this.getCatLogList()
        return
      }
      // this.$indicator.open({
      //   text: '加载中...',
      //   spinnerType: 'fading-circle'
      // })
      if (this.num) {
        this.list.splice(this.num, 1)
      }
      // 下拉刷新时如果获取目录列表（无数据时会获取）
      this.getVideoList(listData => {
        clearTimeout(this.timer)
        this.topBol = true
        this.list = [...listData, ...this.list]
        this.$nextTick(() => {
          this.$store.commit('common_set_dblickLoading', 0)
          this.scroll.finishPullDown()
          this.scroll.refresh()
          this.$indicator.close()
          // 刷新后重启上拉加载
          this.scroll.openPullUp()
          this.timer = setTimeout(() => {
            this.topBol = false
          }, 2000)
          this.num = listData.length
          // 刷新后重启上拉加载
          this.scroll.openPullUp()
        })
      })
    },
    // 获取上拉列表
    upList () {
      if (this.downBol) {  // 防止鼠标的多次加载
        setTimeout(() => {
          this.downBol = true
        }, 800)
        return
      }
      this.loading = 1
      this.$emit('downFn', true)
      let data = {}
      data.columnid = this.columnid
      this.getVideoList(listData => {
        this.loading = 0
        this.list = [...this.list, ...listData]
        this.$emit('downFn', false)
        if (this.list.length === 0) {
          this.scroll.closePullUp()
        }
        this.$nextTick(() => {
          this.scroll.finishPullUp()
          this.scroll.refresh()
          this.downBol = false
        })
      })
    },
    getVideoList (cb) {
      let constant
      let item = this.videoNav[this.activeIndex]
      constant = 'VIDEO_LISTS'
      let data = {
        category: item.en,
        page: this.page
      }
      data.ad_position = 3
      if (this.share.videoPage[data.category]) {
        data.page = this.share.videoPage[data.category]
        this.page = data.page
      }
      this.$api.get(constant, data, res => {
        let listData = res.data.data
        this.page++
        if (this.page >= res.data.last_page) {
          this.setVideoPage({name: data.category, page: 1})
        } else {
          this.setVideoPage({name: data.category, page: this.page})
        }

        if (typeof cb === 'function') {
          cb && cb(listData)
        } else {
          // this.comment_video_list(listData)
          this.list = listData
          this.$nextTick(() => {
            this.loadData()
            if (this.scroll) {
              this.scroll.scrollTo(0, 0, 1000)
            }
          })
        }
      }, () => {
        this.downBol = false
      })
    },
    getCatLogList () {
      if (this.videoNav.length <= 1) {
        this.$api.get('CATEGORY', {}, res => {
          let arr = []
          for (let v of res.data) {
            if (v.name !== '关注' && v.name !== '直播') {
              arr.push(v)
            }
          }
          this.videoNav = arr
          this.loadData()
        })
      }
    },
    // 获取视频列表数据
    getCatModelData () {
      if (this.videoNav.length <= 1) {
        this.$api.get('CATEGORY', {}, res => {
          let arr = []
          for (let v of res.data) {
            if (v.name !== '关注' && v.name !== '直播') {
              arr.push(v)
            }
          }
          this.videoNav = arr
          this.getVideoList()
          this.loadData()
        })
      }
    },
    // 初始化
    initData () {
      if (this.videoNav.length === 0) {
        // this.$indicator.open({
        //   text: '加载中...',
        //   spinnerType: 'fading-circle'
        // })
        this.$api.get('CATEGORY', {}, res => {
          this.videoNav = res.data
          this.getVideoList()
        })
      }
    },
    // 更新需要置灰的下标
    upDate (index) {
      this.indexGray = index
    },
    // 刷新列表是否关注
    isAttention () {
      let addArr = []
      let cancelArr = []
      if (this.$utils.cache('add_attention')) {
        addArr = JSON.parse(this.$utils.cache('add_attention')) || []         // 增加的数组
      }
      if (this.$utils.cache('cancel_attention')) {
        cancelArr = JSON.parse(this.$utils.cache('cancel_attention'))   // 删除的数组
      }
      for (let i in addArr) {
        for (let k in this.list) {
          if (addArr[i] === this.list[k].mid) {
            this.list[k].is_follow = 1
          }
        }
      }
      for (let i in cancelArr) {
        for (let k in this.list) {
          if (cancelArr[i] === this.list[k].mid) {
            this.list[k].is_follow = 1
          }
        }
      }
      this.$utils.cache('add_attention', null)
      this.$utils.cache('cancel_attention', null)
    }
  },
  watch: {
    videoNav (value) {
      this.$emit('getVideoList', value)
    },
    swichId (index) {
      this.switchVideo(this.videoNav[index], index)
    },
    'commonState.dbClick' () {
      if (this.commonState.columnid * 1 === 44) {
        this.downList()
      }
    }
  },
  activated () {
    this.loadData()
  },
  computed: mapState({
    tempList: state => state.comment.videoList,
    share: state => state.share,
    commonState: state => state.common
  }),
  mounted () {
    // this.getCatModelData()
    this.timer = setTimeout(() => {
      this.initData()
      this.activeIndex = 0
    }, 500)
  },
  destroyed () {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  },
  deactivated () {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  },
  components: {
    videoList
  }
}
</script>
<style lang="scss" scoped>
#videoList{
  overflow: hidden;
  position: relative!important;
  width: 750px;
  #videoChannel{
    height: 90px;
    box-sizing: border-box;
    background: #fff;
    position: fixed;
    z-index: 20;
    left: 0;
    right: 0;
    width: 750px;
    line-height: 90px;
    overflow-y: hidden;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    .v-video-channelbox{
      white-space: nowrap;
      padding: 0 12px;
      width: auto;
      min-width: 4500px;
      &>div{
        display: inline-block;
        font-size: 32px;
        padding: 0 10px;
        margin: 0 10px;
        background:#fff;
        height: 40px;
        line-height: 40px;
      }
      &>div.active{
        background: #b2b2b2;
        color: #fff;
        border-radius: 5px;
      }
     
    }
  }
  .wrapper{
    &.isBrowser {
      height: calc(100vh - 382px);
    }
    height: calc(100vh - 282px);
    margin-top: 90px;
    position: relative;
    .topTxt{
      background: #FCEAC5;
      height: 66px;
      overflow: hidden;
      color: #fa0;
    }
    .listBox{
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  .testContent {
    position: absolute;
    height: 800px;
    left: 0;
    right: 0;
    top: 300px;
    bottom: 0;
    color: #fff;
    max-width: 100%;
    z-index: 10;
    background: rgba($color: #f00, $alpha: .4);
    overflow: hidden;
    .childEl {
      white-space: nowrap;
      min-width: 2000px;
      background: #fa0;
      line-height: 90px;
    }
  }
}
</style>



